default:root {
    --img_Cont_height: 0vh;
    --img_height: 0vh;
}

body {
    margin: 0
}

.default, .Counter, .notif {
    border-radius: 5px;
    border-color: #000000;
    background-color: #a3a3a3;
    border-width: 2px;
    border-style: solid;
    height: 40px;
    font-weight: bold;
    /* position: absolute; */
}

.default {
    width: 125px;
    font-size: medium;
}

.Counter {
    width: 50px;
    font-size: 2e1;
}

.notif {
    width: 200px;
    font-size: large;
    line-height: 40px;
    text-align: center;
}

table, th, tr, td {
    border:1px solid black;
    border-collapse: collapse;
}

#Prod_Info_Table {
    position: absolute;
    height: 60vh;
    width: 25vw;
    left: -25vw;
}

#Prod_Info_Table > tr {
    height: 10%;
}

#Basket_Table {
    position: absolute;
    height: 100vh;
    width: 80vw;
}

#Headers {
    height: 10vh;
}

.Prod_IMG_Container {
    height: var(--img_Cont_height);
}

img {
    height: var(--img_height);
}

#PopUp_Table {
    background-color: #a3a3a3;
    width: 30vw;
}

#PopUp_Table > tr > td {
    padding: 10px;
}

td, th {
    text-align: center;
    font-size: x-large;
    font-weight: bold;
}